.top {
  padding-top: 10%;
  padding-bottom: 10%;
  background-color: white;
  display: flex;
  justify-content: center;
}
.top1 {
  padding-top: 10%;
  padding-bottom: 10%;
  background-color: white;
  display: flex;
  justify-content: center;
  border-bottom: medium black solid;
}
.cake {
  transition-property: all;
  transition-duration: 0.5s;
  width: 20%;
  padding: 14% 3%;
  padding-top: 1%;

  padding-bottom: 3%;
  background-color: white;
  color: white;
  border-radius: 8px;
  background-image: linear-gradient(rgb(123, 123, 226), rgb(49, 115, 238));
}
.cake:hover {
  box-shadow: 0px 0px 20px blue;
}
.cake1 {
  background-image: url("header_img3.png");
  padding: 30%;
  background-size: cover;
  border: medium black solid;
}
.cookie {
  transition-property: all;
  transition-duration: 0.5s;
  width: 20%;
  padding: 14% 3%;
  padding-top: 1%;
  margin-left: 3%;
  padding-bottom: 3%;
  background-color: white;
  border-radius: 8px;
  color: white;
  background-image: linear-gradient(rgb(123, 123, 226), rgb(49, 115, 238));
}
.cookie:hover {
  box-shadow: 0px 0px 20px blue;
}
.cookie1 {
  background-image: url("love.png");
  padding: 30%;
  background-size: cover;
  border: medium black solid;
}

.more-week {
  transition-property: all;
  transition-duration: 0.5s;
  color: rgb(0, 0, 0);
  font-weight: 900;
  font-family: sans-serif;
  padding: 2% 1%;
  text-align: center;
  cursor: pointer;

  width: 100%;
  margin-top: 2%;
  margin-bottom: 1%;
  background-color: white;
}

.more-week:hover {
  box-shadow: 0px 0px 15px rgb(0, 0, 0);
}

.more-week1 {
  transition-property: all;
  transition-duration: 0.5s;
  color: rgb(0, 0, 0);
  font-weight: 900;
  font-family: sans-serif;
  padding: 2% 1%;
  text-align: center;
  cursor: pointer;

  width: 100%;
  margin-top: 2%;
  margin-bottom: 1%;
  background-color: white;
}

.more-week1:hover {
  box-shadow: 0px 0px 10px rgb(0, 0, 0);
}
