.navbar1 {
  margin-right: 50%;
  background-color: white;
  display: flex;
  justify-content: flex-end;
  align-items: baseline;
  width: 100%;
}

.header_links1 {
  margin-top: 5%;
  color: rgb(79, 34, 238);
  text-decoration: none;
  margin-left: 50%;
  font-family: sans-serif;
  font-size: 160%;
  font-weight: 600;
}
.header_links1:hover {
  color: rgb(255, 255, 255);
}

.logo1 {
  cursor: pointer;
  margin-left: 100%;
}
.Contact {
  background-color: white;
}
.contact-me {
  background-color: white;
  margin: 0;
  text-align: center;
  font-size: 300%;
  padding-top: 10%;
}
.sub-title {
  background-color: white;
  margin: 0;
  text-align: center;
  padding-bottom: 5%;
  padding-top: 1%;
}
.contact-form {
  background-color: white;
  padding-left: 5%;
  box-shadow: 0px 0px 60px;
  border-radius: 8px;
  margin: 0 20%;
}
.Last-Name {
  margin-left: 2%;
  border-radius: 27px;
  padding: 1%;
  width: 40%;
  margin-top: 3%;
  border: medium black solid;
  outline: none;
}

.Last-Name:focus {
  box-shadow: 0px 0px 20px;
}
.Email {
  margin-left: 2%;
  border-radius: 27px;
  padding: 1%;
  width: 85%;
  margin-top: 3%;
  border: medium black solid;
  outline: none;
}

.Email:focus {
  box-shadow: 0px 0px 20px;
}
.Message {
  margin-left: 2%;
  border-radius: 27px;
  padding: 1%;
  width: 85%;
  padding-bottom: 20%;
  margin-top: 3%;
  margin-bottom: 3%;
  border: medium black solid;
  outline: none;
}

.Message:focus {
  box-shadow: 0px 0px 20px;
}
.padding {
  padding: 5%;
}

.submit {
  font-weight: 800;
  background-color: white;
  cursor: pointer;
  border-radius: 27px;
  padding: 1% 5%;
  outline: none;
  border: medium black solid;
  margin-left: 37%;
  margin-bottom: 5%;
}

.submit:hover {
  background-color: black;
  color: white;
}
