body {
  margin: 0;
}

.header {
  background-image: url("header_img1.png");
  background-repeat: no-repeat;
  background-size: cover;
  padding-bottom: 10%;
  padding-top: 5%;
  animation: header 15s infinite;
  animation-timing-function: ease-in-out;
  background-attachment: fixed;
}

@keyframes header {
  0% {
    background-image: url("header_img1.png");
    background-repeat: no-repeat;
    background-size: cover;
  }
  33% {
    background-image: url("header_img2.png");
    background-repeat: no-repeat;
    background-size: cover;
  }
  66% {
    background-image: url("header_img3.png");
    background-repeat: no-repeat;
    background-size: cover;
  }
}

.products {
  padding-top: 5%;
  margin-left: 10%;
  margin-left: 10%;
}

.text-header {
  font-weight: 900;
  font-family: sans-serif;
  font-size: 300%;
  color: rgb(0, 0, 0);
  margin-bottom: 0;
  text-align: left;
  text-shadow: 2px 2px rgb(255, 255, 255);
}
.text-subheader {
  font-family: sans-serif;
  color: rgb(0, 0, 0);
  text-shadow: 2px 2px rgb(255, 255, 255);
  font-size: 200%;
  width: 100%;
  text-align: left;
}

.button {
  text-decoration: none !important;
  padding: 1% 5%;
  border: medium solid rgb(0, 0, 0);
  width: 8%;
  text-align: center;
  font-family: sans-serif;
  font-weight: 900;
  font-size: 120%;
  color: rgb(0, 0, 0);
  border-radius: 32px;
  margin-top: 2%;
  text-shadow: 2px 2px rgb(255, 255, 255);
  background-color: rgb(226, 226, 226);
}
.button:hover {
  background-color: black;
  border: medium solid rgb(0, 0, 0);
  text-shadow: none;
  color: white;
}

.button-link {
  text-decoration: none !important;
}

.img {
  width: 100%;
  padding: 6%;
  margin-left: 31%;
  background-image: url("thankyou.png");
  background-repeat: no-repeat;
  background-color: white;
}

.text {
  background-color: white;
  margin-top: 0;
  text-align: center;
  font-family: sans-serif;
}

.thank {
  background-color: white;
  padding-bottom: 7%;
}

.us {
  display: flex;
  text-align: center;
  width: 100%;
  border-bottom: medium black solid;
}

.us-buttons {
  width: 100%;
  padding: 10%;
  font-size: 300%;
  transition-property: all;
  transition-duration: 1s;

  font-family: sans-serif;
  cursor: pointer;
  border-top: none;
  font-weight: 600;
  background-color: white;
  color: black;
  text-decoration: none;
}

.us-buttons:hover {
  background-color: rgb(49, 115, 238);
  color: white;
}
#about {
  border-left: medium black solid;
}

body {
  margin: 0;
  padding: 0;
}
#root {
  height: 120%;
  padding-bottom: 2%;
  background-color: black;
}
.footer {
  background-color: black;
  border-top: thin black solid;

  margin-bottom: 0;
  bottom: 0;
  left: 0;
}

.home {
  color: rgb(255, 255, 255);
  text-align: center;
  padding-top: 4%;
  font-family: sans-serif;
}
.home-link {
  text-decoration: none;
  align-items: center;
}
.home-button {
  align-items: center;
  background-color: rgb(0, 0, 0);
  border: thin rgb(255, 255, 255) solid;
  width: 8%;
  text-align: center;
  margin-left: 45%;
  padding: 1% 3%;
  align-items: center;
  cursor: pointer;
  color: rgb(255, 255, 255);
  font-family: sans-serif;
  font-weight: 600;
  text-decoration: none;
}
.home-button:hover {
  background-color: rgb(255, 255, 255);
  color: rgb(0, 0, 0);
}

.home-div {
  width: 50%;
  height: 100%;
  margin-left: 25%;
  padding-bottom: 2%;
  border-bottom: thin rgb(255, 255, 255) solid;
}

.link1 {
  text-decoration: none;

  text-align: center;
  color: rgb(255, 255, 255);
  font-family: sans-serif;
  font-size: 150%;
}

.link {
  text-decoration: none;
  margin-left: 2%;
  text-align: center;
  color: rgb(255, 255, 255);
  font-family: sans-serif;
  font-size: 150%;
}

.links1 {
  display: flex;
  justify-content: center;
}

.credit {
  color: rgb(255, 255, 255);
  padding: 0 2%;
  font-family: sans-serif;
}

.bottom {
  display: flex;
  align-items: center;
}

.ig {
  height: 2%;
  width: 2%;
  margin-left: 75%;
}

.love {
  margin: 0;
  background-image: url("love.png");
  padding: 10%;
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  border-top: medium black solid;
  border-bottom: medium black solid;
  color: white;
  text-shadow: 2px 2px black;
  font-size: 300%;
  font-weight: 700;
}

.linking {
  text-decoration: none;
}
